
/* src/styles/Desktop.css */


.menu-separator {
    height: 1px;
    background-color: #808080;
    margin: 2px 0;
  }
  
  .desktop-icon {
    width: 100px; /* Maintain the width */
    text-align: center;
    margin: 10px 0; /* Adjust the margin to align vertically */
    cursor: pointer;
    user-select: none;
    position: absolute;
    transition: all 0.2s ease; /* Smooth transition for hover and selection effects */
  }
  
  .desktop-icon-image {
    width: 50px; /* Set the size to 50px */
    height: 50px; /* Set the size to 50px */
    margin-bottom: 10px; /* Add some space between the icon and text */
  }
  
  .desktop-icon-name {
    font-size: 14px; /* Increase the font size */
    color: black;
    display: block;
    margin-top: 5px;
  }
  
  .selected {
    opacity: 0.8; /* Slightly transparent for a soft highlight */
  }
  
  .selected .desktop-icon-image {
    filter: brightness(1.2); /* Slightly increase brightness for the icon */
  }
  
  .selected .desktop-icon-name {
    color: white; /* Keep text color white for contrast */
    background-color: rgba(0, 120, 215, 0.3); /* Transparent blue background for highlight */
    border-radius: 3px; /* Slightly rounded corners */
    padding: 2px 4px; /* Padding around the text */
    display: inline-block; /* Ensures the background covers only the text */
  }

  .main-content {
    flex: 1;
    width: 100%;
    overflow: hidden; /* Prevent overflow within the main content */
  }
  
  .taskbar {
    width: 100%;
    height: 40px;
    background-color: #c3c3c3; /* Taskbar background color */
    display: flex;
    justify-content: space-between; /* Space between start button and clock */
    align-items: center;
    position: absolute;
    bottom: 0;
  }

  .container.show-background {
    background-image: url('/public/background.png'); /* Set background image */
    background-size: cover; /* Cover the entire container */
    background-position: center; /* Center the background image */
  }
  
  .container.loaded {
    background-color: rgb(51, 102, 153); /* Updated background color */
    color: black; /* Change text color for final state if needed */
    justify-content: center; /* Center the login container vertically */
    align-items: center; /* Center the login container horizontally */
  }